.btn-home {
  color: #fff;
  cursor: pointer;
  background-color: #1e90ff;
  border: none;
  margin-bottom: 10px;
  padding: 12px 16px;
  font-size: 16px;
}

.btn-home:hover {
  background-color: #4169e1;
}

.btn-actions {
  width: 200px;
  height: 100px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3px;
  color: #524f4e;
  background: #fff;
  border-radius: 45px;
  margin: 10px 20px;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: 600;
  line-height: 45px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
}

.btn-actions:hover {
  color: #fff;
  background: #576e65;
  transform: translateY(-7px);
  box-shadow: 0 15px 20px rgba(52, 55, 54, .4);
}

.btn-data {
  color: #fff;
  background-color: #2a591d;
  border: 1px solid #2a591d;
  padding: .75em 1.75em;
  font-weight: 500;
  line-height: 1;
  display: inline-block;
}

.btn-data:hover {
  cursor: pointer;
}

.btn-data[disabled], .btn-data[disabled]:hover {
  cursor: not-allowed;
  color: #000;
  background-color: rgba(0, 0, 0, .1);
  border: 1px solid rgba(0, 0, 0, .1);
}

